import type {
  MoFactory,
} from '@ali/4ever-cangjie';
import createSerializer from './serializer';
import { headingTypes } from './models'

const factory: MoFactory = () => ({
  key: 'heading',
  serializer: createSerializer(),
  models: [],
  schemas: Object.keys(headingTypes).map((type) => ({
    type,
    isBlock: true,
  })),
});

export default factory;
